:root {
  --mint: #3cb489;
  --mint-light: #7dd8b5;
}

.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
}

.Content {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
}

.Body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5%;
}

.Title {
  font-weight: bold;
  font-size: xx-large;
  color: var(--mint);
}

.Item {
  display: flex;
  flex-direction: column;
  padding: 12px;
}

.Item-Title {
  font-weight: bold;
}

.Item-Details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.SubItem {
  padding-top: 6px;
  padding-left: 6px;
  padding-right: 6px;
}

a {
  color: var(--mint);
}

a:hover {
  color: var(--mint-light);
}

.Logo {
  height: 30%;
  width: 30%;
  padding: 5%;
}

.Logo img {
  height: 100%;
  width: 100%;
}

.Date {
  color: gray;
}

.GlassesOverlay {
 z-index: 1;
 width: 24%;
 position: fixed;
/* padding-top: 10%;*/
 justify-content: center;
/* border: 2px solid blue;*/
}

.Glasses {
  display: flex;
  justify-content: space-around;
  margin-top: 42%;
  transition: transform ease 300ms;
}

.Glasses:hover {
  transform: translateY(-3px);
}

.InfiniteAnimation {
    animation: MoveUpDown 1s linear infinite;
}

.LeftCircle {
  border: .1em solid black;
  margin-left: 22%;
  border-radius: 50%;
  width: 23%;
  /*This fills up the circle so it actually has 100% height*/
  padding-bottom: 23%;
}

.RightCircle {
  border: .1em solid black;
  margin-right: 22%;
  border-radius: 50%;
  width: 23%;
  /*This fills up the circle so it actually has 100% height*/
  padding-bottom: 23%;
}

@keyframes MoveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(2px);
  }
}